<template>
  <div>
    <van-nav-bar title="" left-text="" right-text="" left-arrow @click-left="$router.back()" @click-right="$router.push({name: 'PayAccount'})" />

    <van-row>
      <van-col span="24">
        <van-steps :active="step">
          <van-step></van-step>
          <van-step></van-step>
          <van-step></van-step>
        </van-steps>
      </van-col>
    </van-row>

    <van-row>
      <!-- <van-col span="24" class="tips">
        <br> 5
      </van-col> -->

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field value="" placeholder="" label="" disabled/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.cityText" placeholder="" label="" @click="showCityPicker = true" />
          <van-picker show-toolbar title="" :columns="columns" @cancel="onCancel" @confirm="onConfirm" v-show="showCityPicker" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.detail" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.postal" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.number" placeholder="" label="" />
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field value="" placeholder="" label="" disabled/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group style="position:relative;">
          <van-radio-group v-model="form.shipSet" class="radio">
            <van-cell>
              <van-radio name="1"></van-radio>
              <van-radio name="2"></van-radio>
              <van-radio name="3"></van-radio>
            </van-cell>

          </van-radio-group>
        </van-cell-group>
      </van-col>

    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      step: 1,
      form: {},
      columns: [
        {
          id: '220100',
          text: ''
        },
        {
          id: '220200',
          text: ''
        },
        {
          id: '220300',
          text: ''
        },
        {
          id: '220400',
          text: ''
        },
        {
          id: '220500',
          text: ''
        },
        {
          id: '220600',
          text: ''
        },
        {
          id: '220700',
          text: ''
        },
        {
          id: '220800',
          text: ''
        }
      ],
      showCityPicker: false
    }
  },
  methods: {
    onConfirm(value, index) {
      this.form.cityId = value.id
      this.form.cityText = value.text
      this.showCityPicker = false
    },
    onCancel() {
      this.showCityPicker = false
    },
    handlePost() {
      const params = this.form
      this.$api.SaveShipInfo(params).then(res => {
        if (res.data.code == 200) {
          this.$router.push({ name: 'PayAccount' })
        } else {
          this.$toast.fail(res.data.msg)
        }
      })
    }
  }
}
</script>


<style lang="scss" scoped>
.tips {
  color: red;
  font-size: 11px;
  padding: 10px;
}

.radio {
  // display: flex;
  // padding: 10px;
  // position: absolute;
  // z-index: 1000;
  // left: 80px;
  // top: 0;
}
</style>



