<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="$router.back()" />

    <van-row>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.name" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.id" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>

      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.phoneNo" placeholder="" label="" required/>
        </van-cell-group>
      </van-col>
      <van-col span="24">
        <van-cell-group>
          <van-field v-model="form.shipNo" placeholder="" label="" />
        </van-cell-group>
      </van-col>

      <van-col span="24" class="active-name">
        
      </van-col>
      <van-col span="24" class="active-cnt">
        <br> 
      </van-col>
      <van-col span="24" class="active-tips">
        10000
      </van-col>
      <van-col span="24" style="margin-top:20px;">
        <van-button type="primary" bottom-action class="btn" @click="handleSubmit" :disabled="btnDisabled"></van-button>
      </van-col>
    </van-row>
  </div>
</template>


<script>
export default {
  data() {
    return {
      form: {
        id: '',
        name: '',
        phoneNo: '',
        shipNo: ''
      },
      btnDisabled: true
    }
  },
  watch: {
    form: {
      deep: true,
      immediate: true,
      handler(val) {
        if (val.id === '' || val.name === '' || val.phoneNo === '') {
          this.btnDisabled = true
        } else {
          this.btnDisabled = false
        }
      }
    }
  },
  methods: {
    handleSubmit() {
      const vm = this
      vm.$api
        .activeCard(this.form)
        .then(res => {
          if (res.data.code == '200') {
            vm.$dialog
              .alert({
                message:
                  'xx'
              })
              .then(() => {
                vm.$router.push({ name: 'Home' })
              })
          } else {
            vm.$dialog
              .alert({
                message:
                  'xxxx'
              })
              .then(() => {
                vm.$router.push({ name: 'Home' })
              })
          }
        })
        .catch(err => {
          vm.$toast.fail(JSON.stringify(err))
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.active-name,
.active-cnt,
.active-tips {
  color: red;
  font-size: 14px;
}

.active-name {
  padding: 20px;
}

.active-cnt,
.active-tips {
  padding: 0 20px;
}
</style>





